.icon-area {
    background-color: transparent !important;
    color: #FFF;
    box-shadow: none !important;
}
.icon-container {
    display: flex;
    align-items: center;
}
.icon-container a {
    display: block;
    padding: 5px;
    flex: 1;
    font-size: 10px;
    margin: 0 2px;
    background-color: #FF002F;
    text-decoration: none;
    color: #FFF;
}
.icon-container a i {
    padding-top: 5px;
}
.icon-container a span {
    display: block;
    padding-top: 5px;
}