.hide {
    display: none;
}
.show {
    display: block;
}
.wrapper {
    padding: 10px 16px;
    background-color: #e2e0e0;
    height: 100vh;
}
.screen-list {
    min-height: 60px;
    margin-bottom: 10px;
}
.upcoming-tips {
    position: relative;
}
.screen-list .tips-title {
    margin: 0;
    text-align: center;
    padding: 5px 0 5px 10px;
    font-size: 18px;
}
.screen-list .tips-subtitle {
    margin: 0;
    text-align: center;
    padding: 5px 0 0 10px;
    font-size: 14px;
}
.screen-list .tips-title .date-text {
    font-size: 12px;
    font-weight: normal !important;
}
.wrapper .inner-conainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px 5px 5px 0px;
}
.screen-list .action-tracker {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: flex-end;
    justify-content: center;
    margin-bottom: 10px;
    padding: 10px 10px 5px 0;
}
.btn
{
    display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color:red;
    color:white;
}
