.hide {
    display: none;
}
.show {
    display: block;
}
.wrapper {
    padding: 10px 16px;
    background-color: #e2e0e0;
    height: 100vh;
}
.screen-list {
    min-height: 60px;
    margin-bottom: 10px;
}
.upcoming-tips {
    position: relative;
}
.screen-list .tips-title {
    margin: 0;
    text-align: left;
    padding: 5px 0 5px 10px;
    font-size: 18px;
}
.screen-list .tips-subtitle {
    margin: 0;
    text-align: left;
    padding: 5px 0 0 10px;
    font-size: 14px;
}
.screen-list .tips-title.signee,
.screen-list .tips-subtitle.signee {
    text-align: left;
}
.screen-list .tips-title .date-text {
    font-size: 12px;
    font-weight: normal !important;
}
.wrapper .inner-conainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px 5px 5px 0px;
}
.inner-conainer .timer-list {
    flex-grow: 1;
    justify-content: center;
    align-items: center;
}
.inner-conainer .separator {
    border: 1px solid gray;
    height: 50px;
    opacity: 0.5;
}
.inner-conainer .trip-details {
    flex-grow: 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 5px 5px 5px 10px;
}
.inner-conainer .timer-list .time {
    font-size: 16px;
    font-weight: bold;
}
.inner-conainer .timer-list .timer-content {
    font-size: 12px;
}
.inner-conainer .trip-details .trip-location {
    font-size: 12px;
    margin-top: 5px;
}
.inner-conainer .trip-details .marker {
    color:  #FF002F;
}
.inner-conainer .trip-details .location-text {
    padding-left: 5px;
}
.inner-conainer .trip-details .trip-name {
    font-size: 14px;
}
.screen-list .action-tracker {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-bottom: 10px;
    padding: 10px 10px 5px 0;
}
.screen-list .action-tracker .action-btn {
   background-color: #FF002F;
   margin-left: 5px;
   border-radius: 5px;
   border: none;
   color: white;
   padding: 10px 10px;
   text-align: center;
   text-decoration: none;
   display: inline-block;
   font-size: 12px;
}
.trip-action {
    text-decoration: none;
    padding: 2px 5px;
    border: 1px solid #ccc;
    display: inline-block;
    margin: 5px 2px;
    border-radius: 3px;
    color: #828282;
}
.more-icon {
    position: absolute;
    right: 15px;
    bottom: 5%;
}
.more-icon i {
    display: block;
    font-size: 18px;
}
.more-icon {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
}
.user-status {
    animation: fadeIn 300ms;
    min-height: 30px !important;
}
.success-msg {
    color: green;
    padding: 10px;
    font-size: 16px;
}
.call-driver-icon {
    text-decoration: none;
}
.sosCallIcon {
    display: inline-block;
    width: 45px;
    height: 45px;
    position: absolute;
    right: 1%;
    top: 1%;
  }
  .end-trip-btn {
      margin-left: 225px !important;
  }