.body {
  height: 100vh;
  /* background-color: #F05D5E; */
  background-color: white;  
  margin-top: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
input:-internal-autofill-selected {
  background-color: rgb(250, 255, 189) !important;
}

.loginBox {
  position: relative;
  margin : auto;
  top: 100px;
  width: 80%;
  height: 50vh;
  /* background-color: #F05D5E ; */
}

.inputFieldIcon {
  color: black;
  padding-top: 5px;
}

.textBoxBackground {
  /* padding: 10; */
  /* background-color: grey; */
}

.inputBox {
  height: 30px;
  width: 200px;
  /* padding: 10px; */
  margin: 10px;
  background-color: white;
  color: black;
  font-weight: bold;
  border-style: none;
  border: 2px solid black;
}

/* input:focus {
  color: #ff0000;
} */

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: black;
  opacity: 1; /* Firefox */
}

.loginButton {
  border: 2px solid black;
  color: black;
  background-color: white;
  padding: 10px;
  margin-top: 10px;
  width: 100px;
}

.matrialLoginButton {
  color: black
}
.matrialLoginButton.login-cta {
  position: relative;
  display: inline-block;
  padding-right: 30px;
  padding-left: 30px;
  height: 40px;
  outline: none;
  border: none;
  border-radius: 20px;
  color: white;
  font-weight: 500;
  font-size: 17px;
  transition: all .5s ease;  
  cursor: pointer;
  background: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  letter-spacing: 2px;
  margin-top: 25px;
}
.body input.inputBox {
  width: 79%;
  height: 40px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 15px;
  margin-bottom: 10px;
  box-sizing: border-box;
  border-radius: 0px 25px 25px 2px;
  letter-spacing: 1px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.icon-div {
  display: inline-block;
  border: 2px solid black;
  height: 35px;
  width: 10%;
  border-radius: 20px 0 0 20px;
}
.body .MuiSvgIcon-root-8 {
  position: relative;
  top: 4px;

}